@use '../base/variables' as v;
@use '../base/mixins' as m;
html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,*::before,*::after{
    box-sizing: inherit;
}
body {
    min-height: 100vh;
    font-family: v.$font-main;
    font-weight: v.$medium;
    line-height: 1;
}
@include m.desktop {
    .container{
        min-height: 100vh;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        max-width: v.$mobile;
        width: 95%;
        @include m.desktop {
            max-width: v.$desktop;
        }
    }
}

a{
    text-decoration: none;
}
